/* =========================
   Navbar Base Styles
   ========================= */
.navbar {
  background: linear-gradient(90deg, rgba(59,130,246,0.07) 0%, rgba(255,255,255,0.97) 100%);
  border-bottom: 1px solid #e5e7eb;
  height: 70px;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 4px 16px rgba(59,130,246,0.10);
  border-radius: 0 0 18px 18px;
  backdrop-filter: blur(6px);
  transition: box-shadow 0.2s;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px;
  box-sizing: border-box;
}

.logo {
  font-size: 1.7rem;
  font-weight: 900;
  color: #1f2937;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 6px 18px 6px 0;
  border-radius: 10px;
  transition: background 0.2s;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", sans-serif;
  letter-spacing: 0.5px;
}

.logo-icon {
  font-size: 2.1rem;
  margin-right: 2px;
}

.nav-links {
  display: flex;
  gap: 22px;
  align-items: center;
  transition: all 0.3s ease;
}

.nav-link {
  color: #4b5563;
  text-decoration: none;
  font-weight: 500;
  font-size: 1.05rem;
  cursor: pointer;
  background: none;
  border: none;
  transition: color 0.3s, background 0.2s;
  padding: 8px 10px;
  border-radius: 6px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", sans-serif;
}

.nav-link:hover {
  background: rgba(59,130,246,0.1);
}

.signup-btn {
  color: #fff;
  background: #2563eb;
  padding: 8px 24px;
  border-radius: 8px;
  font-weight: 700;
  box-shadow: 0 2px 8px rgba(59,130,246,0.10);
  transition: all 0.2s;
  border: none;
}

.signup-btn:hover {
  background: #1d4ed8;
}

.avatar {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-left: 8px;
  margin-right: 2px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: rgba(37,99,235,0.08);
  box-shadow: 0 2px 8px rgba(59,130,246,0.10);
  cursor: pointer;
  transition: background 0.2s;
}

.avatar:hover {
  background: rgba(37,99,235,0.15);
}

/* =========================
   Hamburger Styles
   ========================= */
.hamburger {
  display: none; /* hidden on desktop */
  flex-direction: column;
  justify-content: space-around;
  width: 28px;
  height: 22px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 1100;
}

.hamburger-bar {
  width: 100%;
  height: 3px;
  background: #1f2937;
  border-radius: 2px;
  transition: all 0.3s ease;
}

/* Rotate bars into X */
.hamburger.open .hamburger-bar:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}
.hamburger.open .hamburger-bar:nth-child(2) {
  opacity: 0;
}
.hamburger.open .hamburger-bar:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -5px);
}

/* =========================
   Mobile Styles
   ========================= */
@media (max-width: 900px) {
  .nav-container {
    padding: 0 16px;
  }
  .logo {
    font-size: 1.4rem;
    padding: 6px 8px 6px 0;
  }
  .signup-btn {
    padding: 8px 16px;
    font-size: 1rem;
  }
}

@media (max-width: 600px) {
  /* Show hamburger */
  .hamburger {
    display: flex;
  }

  /* Slide-in nav links */
  .nav-links {
    position: fixed;
    top: 70px; /* below navbar */
    right: 0;
    width: 220px;
    height: calc(100vh - 70px);
    background: #fff;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    gap: 16px;
    transform: translateX(100%);
    transition: transform 0.3s ease;
    box-shadow: -2px 0 8px rgba(0,0,0,0.12);
    z-index: 1000;
  }

  .nav-links.open {
    transform: translateX(0);
  }

  .nav-link {
    width: 100%;
    padding: 10px 12px;
    font-size: 1rem;
  }

  .signup-btn {
    padding: 10px 12px;
    width: 100%;
    text-align: center;
  }

  .avatar {
    margin-left: 0;
    margin-bottom: 10px;
  }

  /* Overlay behind menu */
  .nav-overlay {
    position: fixed;
    top: 70px;
    left: 0;
    width: 100vw;
    height: calc(100vh - 70px);
    background: rgba(0, 0, 0, 0.25);
    z-index: 900;
    backdrop-filter: blur(1px);
  }
}
