/* =========================
   Base Styles (Mobile-First)
   ========================= */

.login-container {
  display: flex;
  flex-direction: column; /* Mobile: stack */
  min-height: 100vh;
  background: #fff;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", sans-serif;
}

.login-left-panel {
  flex: 1;
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  padding: 16px;
}

.login-image-bg {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login-overlay-card {
  position: relative; /* mobile: relative instead of absolute */
  top: auto;
  left: auto;
  transform: none;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(59,130,246,0.13);
  padding: 24px;
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1.5px solid #e5e7eb;
  z-index: 2;
}

.login-title {
  font-size: 1.8rem;
  font-weight: 900;
  color: #0f172a;
  margin: 0 0 8px 0;
  text-align: center;
}

.login-subtitle {
  font-size: 0.95rem;
  color: #64748b;
  margin: 0 0 24px 0;
  text-align: center;
}

.login-form {
  width: 100%;
  margin-bottom: 16px;
}

.login-form-group {
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
}

.login-label {
  font-size: 0.85rem;
  font-weight: 700;
  color: #0f172a;
  margin-bottom: 6px;
}

.login-input {
  width: 100%;
  padding: 10px 8px;
  font-size: 1rem;
  border: 1.5px solid #cbd5e1;
  border-radius: 10px;
  background: #f8fafc;
  box-sizing: border-box;
  font-family: inherit;
  outline: none;
  transition: all 0.2s;
}

.login-error-box {
  background: #fee2e2;
  color: #991b1b;
  padding: 10px 12px;
  border-radius: 8px;
  margin-bottom: 12px;
  font-size: 0.9rem;
  border: 1px solid #fecaca;
  width: 100%;
  text-align: center;
}

.login-button {
  width: 100%;
  padding: 10px 0;
  font-size: 1rem;
  font-weight: 700;
  background: linear-gradient(90deg,#2563eb,#3b82f6);
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(59,130,246,0.1);
  transition: all 0.2s;
}

.login-footer {
  text-align: center;
  font-size: 0.9rem;
  color: #64748b;
  margin-top: 16px;
}

.login-link {
  color: #3b82f6;
  text-decoration: none;
  font-weight: 700;
  cursor: pointer;
}

.login-overlay-card svg,
.login-image-bg > div.emoji {
  display: none; /* hide decorative elements on mobile */
}

/* =========================
   Tablet / Medium Screens
   ========================= */
@media (min-width: 600px) {
  .login-container {
    flex-direction: row;
  }
  .login-left-panel {
    flex: 1;
    min-width: 0;
    padding: 0;
  }
  .login-overlay-card {
    padding: 32px 24px;
    max-width: 450px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .login-title {
    font-size: 2rem;
  }
  .login-subtitle {
    font-size: 1rem;
    margin-bottom: 32px;
  }
  .login-input {
    padding: 12px 10px;
    font-size: 1.05rem;
  }
  .login-button {
    padding: 12px 0;
    font-size: 1.05rem;
  }
  .login-overlay-card svg {
    display: block; /* show SVG decorations on larger screens */
  }
}

/* =========================
   Desktop / Large Screens
   ========================= */
@media (min-width: 900px) {
  .login-overlay-card {
    max-width: 480px;
    padding: 40px 32px;
  }
  .login-title {
    font-size: 2.2rem;
  }
  .login-subtitle {
    font-size: 1rem;
  }
  .login-input {
    font-size: 1.1rem;
    padding: 12px 10px;
  }
  .login-button {
    font-size: 1.1rem;
    padding: 12px 0;
  }
}
